export default{
	name:'testype',
	data() {
		return {
			phoneType: '正在检测...',
			smodel:''
		}
	},
	created() {
		uni.getSystemInfo({
			success: res =>{
				let s = res.model
				let num = s.indexOf('(')
				if(num < 0){
					num = s.indexOf('<')
					console.log(num)
					s = s.substring(0,num)
					this.phoneType = s
				}
				else{
					console.log(num)
					s = s.substring(0,num)
					this.phoneType = s
				}
				
				console.log(res)
				this.smodel = res.model
				
			}
		})
	},
	methods:{
		onNext() {
			uni.navigateTo({
				url: '../../pages/selectmodel/selectmodel'
			})
		},
		bindRepair(){
			uni.navigateTo({
				url:'../../pages/selectmodel/selectmodel?model=' + this.smodel
			})
		}
	}
}
